<template>
    <div class="user-promote-month-bill">
        <div class="header row white">
            <div class="header-show column-center">
                <div class="num">{{month}}</div>
                <div class="label sm row" @click="showPop = true">月份<trigonometry color="white" opacity="1" /></div>
            </div>
            <div class="header-show column-center">
                <div class="num">{{order}}</div>
                <div class="label sm">成交笔数</div>
            </div>
            <div class="header-show column-center">
                <price-slice
                    showSubscript
                    Subscript="sm"
                    firstClass="order-price-size"
                    secondClass="order-price-size"
                    color="white"
                    :price="money" 
                />
                <div class="label sm">累计预估收益</div>
            </div>
        </div>
        <div class="content">
            <div class="order-container" v-if="!isEmpty">
                <van-list 
                    v-model="loading"
                    :finished="finished"
                    finished-text="没有更多了"
                    @load="$getMonthOrderDetail"
                >
                    <div class="order-item bg-white mb10" v-for="(item) in orderList" :key="item.order_sn" >
                        <div class="order-header row-between">
                            <div>订单编号:{{item.order_sn}}</div>
                            <div class="white guide-shop-btn row-center">导购订单</div>
                        </div>
                        <div class="order-content row">
                            <div class="order-goods-img">
                                <van-image :src="item.image" width="100%" height="100%" radius="6px" ></van-image>
                            </div>
                            <div class="order-goods-info ml10">
                                <div class="name row sm">{{item.goods_name}}</div>
                                <div class="pre-income muted">预估收益 
                                    <price-slice
                                        showSubscript
                                        subScriptClass="nr"
                                        firstClass="nr"
                                        secondClass="nr"
                                        :color="primaryColor"
                                        weight="bold"
                                        :price="item.money"
                                    />
                                </div>
                            </div>
                        </div>
                        <div class="order-footer row-between">
                            <div class="time muted">{{item.create_time}}</div>
                            <div class="static">{{item.status}}</div>
                        </div>
                    </div>
                </van-list>
            </div>
            <div class="data-null column-center" v-else>
                <img src="@A/images/order_null.png" class="img-null" />
                <div class="muted xs">暂无相关数据～</div>
            </div>
        </div>
        <van-popup v-model="showPop" position="bottom" :style="{ height: '50%' }">
            <van-picker 
            :columns="months" 
            title="月份" 
            show-toolbar
            @cancel="onCancel"
            @confirm="onConfirm" 
            />
        </van-popup>
    </div>
</template>

<script>
import {getMonthOrderDetail} from '@API/user'
import Trigonometry from '@C/Trigonometry/Trigonometry.vue';
const months = [];
for (let i = 1; i <= 12; i++) {
  months.push(i)
}
export default {
    name: "userPromoteMonthBillDetail",
    components: { Trigonometry },
    data() {
        return {
            page: 1,
            loading: true,
            finished: false,
            orderList: [],
            isEmpty: false,
            month: 1,
            year: 2010,
            // 累计预估收益
            money: 0,
            // 成交笔数
            order: 0,
            // 选择器月份
            months: months,
            showPop: false
        }
    },
    methods: {
        $getMonthOrderDetail() {
            let {page, orderList} = this;
            this.loading = true;
            if(this.finished) return;
            getMonthOrderDetail({
                page_no: page,
                month: this.month,
                year: this.year
            }).then(res => {
                if(res.code == 1) {
                    this.loading = false;
                    let {more, list, total_order, total_money} = res.data;
                    this.money = total_money;
                    this.order = total_order;
                    orderList.push(...list);
                    this.orderList = orderList
                    this.page ++;
                    if(!more) {
                        this.finished = true
                    }
                    if(orderList.length <= 0) {
                        this.isEmpty = true;
                        return
                    }
                }
            })
        },
        onConfirm(value, index) {
            this.month = value;
            this.showPop = false;
            this.cleanStatus()
            this.$getMonthOrderDetail()
        },
        cleanStatus() {
            this.page = 1;
            this.loading = true;
            this.finished = false;
            this.orderList = [];
            this.isEmpty = false;
        },
        onCancel() {
            this.showPop = false
        }
    },
    mounted() {
        this.month = this.$route.query.month;
        this.year = this.$route.query.year;
        this.$getMonthOrderDetail()
    }
}
</script>

